import {
  AppBar,
  Avatar,
  Box,
  Divider,
  IconButton,
  Menu,
  MenuItem,
  Stack,
  Theme,
  Toolbar,
} from '@material-ui/core';
import React, { Fragment } from 'react';

import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import NightsStayIcon from '@material-ui/icons/NightsStay';
import StyledIconButton from '../StyledIconButton';
import StyledNavLink from '../StyledNavLink';
import StyledTypography from '../StyledTypography';
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import personalInfo from '../../data/personalInfo';
import routes from '../../data/routes';
import { useAppTheme } from '../../context/AppThemeContext';
import useBreakpoints from '../../hooks/useBreakpoints';

const { PUBLIC_URL } = process.env;
const indexLinks = routes.filter((link) => link.index);
const nonIndexLinks = routes.filter((link) => !link.index);

const ThemeToggler = ({ onClick }: { onClick?: () => void }) => {
  const { theme, toggleMode } = useAppTheme();
  const onToggleClick = async () => {
    toggleMode();
    if (onClick) {
      onClick();
    }
  };
  return (
    <StyledIconButton onClick={onToggleClick}>
      {theme.palette.mode === 'dark' && <WbSunnyIcon />}
      {theme.palette.mode === 'light' && <NightsStayIcon />}
    </StyledIconButton>
  );
};

const DesktopNavigation = () => {
  return (
    <Stack direction="row" spacing={2} alignItems="center">
      {indexLinks.map((indexLink) => (
        <StyledNavLink
          key={indexLink.label}
          to={indexLink.path}
          label={indexLink.label}
          disableActiveStyle={true}>
          <StyledTypography
            variant="button"
            display="inline"
            bold={true}
            capitalized={true}
            spaced={true}
            hoverEffect={true}>
            {indexLink.label}
          </StyledTypography>
        </StyledNavLink>
      ))}
      {nonIndexLinks.map((nonIndexLink) => (
        <Fragment key={nonIndexLink.label}>
          <Divider orientation="vertical" />
          <StyledNavLink
            exact
            to={nonIndexLink.path}
            label={nonIndexLink.label}>
            <StyledTypography
              variant="overline"
              display="inline"
              capitalized={true}
              spaced={true}
              hoverEffect={true}>
              {nonIndexLink.label}
            </StyledTypography>
          </StyledNavLink>
        </Fragment>
      ))}
      <ThemeToggler />
    </Stack>
  );
};

const MobileMenu = () => {
  const [anchorElement, setAnchorElement] = React.useState<null | HTMLElement>(
    null,
  );
  const isMenuOpen = Boolean(anchorElement);

  const openMenuClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorElement(event.currentTarget);
  };

  const onNavigationClick = () => {
    setAnchorElement(null);
  };

  return (
    <>
      <IconButton onClick={openMenuClick}>
        <MenuRoundedIcon />
      </IconButton>
      <Menu
        id="basic-menu"
        anchorEl={anchorElement}
        open={isMenuOpen}
        onClose={onNavigationClick}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}>
        {indexLinks.map((indexLink) => (
          <StyledNavLink
            exact
            to={indexLink.path}
            label={indexLink.label}
            disableActiveStyle={true}
            key={indexLink.label}>
            <MenuItem onClick={onNavigationClick}>
              <StyledTypography
                variant="button"
                display="inline"
                bold={true}
                capitalized={true}
                spaced={true}
                hoverEffect={true}>
                {indexLink.label}
              </StyledTypography>
            </MenuItem>
          </StyledNavLink>
        ))}
        <Divider orientation="horizontal" variant="middle" />
        {nonIndexLinks.map((nonIndexLink) => (
          <StyledNavLink
            exact
            to={nonIndexLink.path}
            label={nonIndexLink.label}
            key={nonIndexLink.label}>
            <MenuItem onClick={onNavigationClick}>
              <StyledTypography
                variant="overline"
                display="inline"
                capitalized={true}
                spaced={true}
                hoverEffect={true}>
                {nonIndexLink.label}
              </StyledTypography>
            </MenuItem>
          </StyledNavLink>
        ))}
        <Divider orientation="horizontal" variant="middle" />
        <Box display="flex" flexDirection="row" justifyContent="center">
          <ThemeToggler onClick={onNavigationClick} />
        </Box>
      </Menu>
    </>
  );
};

const MobileNavigation = () => {
  return (
    <>
      <StyledTypography
        variant="button"
        bold={true}
        capitalized={true}
        spaced={true}
        hoverEffect={true}
        sx={{ flexGrow: 1 }}>
        {personalInfo.fullName}
      </StyledTypography>
      <Box flexDirection="row-reverse" sx={{ py: 1 }}>
        <Avatar
          src={`${PUBLIC_URL}/images/me.jpg`}
          sx={{
            width: 50,
            height: 50,
          }}>
          N M
        </Avatar>
      </Box>
      <MobileMenu />
    </>
  );
};

const Navigation = () => {
  const { md: mdUp = true } = useBreakpoints('up');
  console.debug(mdUp);

  return (
    <>
      <AppBar
        variant="outlined"
        position="sticky"
        sx={{
          alignItems: mdUp ? 'center' : undefined,
          backgroundColor: (theme: Theme) => theme.palette.background.paper,
          color: (theme: Theme) => theme.palette.text.primary,
        }}>
        <Toolbar variant="dense">
          {!mdUp && <MobileNavigation />}
          {mdUp && <DesktopNavigation />}
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Navigation;
