import { IconButton, Link, Theme } from '@material-ui/core';
import React, { ReactNode, useState } from 'react';

import StyledIconButton from '../StyledIconButton';
import contacts from '../../data/contact';

const ContactIcon = ({ children }: { children: ReactNode }) => {
  return (
    <>
      <StyledIconButton>{children}</StyledIconButton>
    </>
  );
};

const ContactIcons = () => {
  return (
    <>
      {contacts.map((contact) => (
        <Link key={contact.label} href={contact.link} target="_blank">
          <ContactIcon>{contact.icon}</ContactIcon>
        </Link>
      ))}
    </>
  );
};

export default ContactIcons;
