import { Avatar, Divider, Stack, Typography } from '@material-ui/core';
import React, { Fragment } from 'react';
import StyledLink, { IStyledLinkLinkProps } from '../StyledLink';

import ContactIcons from '../Contact/ContactIcons';
import StyledButton from '../StyledButton';
import StyledNavLink from '../StyledNavLink';
import StyledTypography from '../StyledTypography';
import jobs from '../../data/resume/job';
import personalInfo from '../../data/personalInfo';
import useBreakpoints from '../../hooks/useBreakpoints';
import { useRouteMatch } from 'react-router-dom';

const { PUBLIC_URL } = process.env; // set automatically from package.json:homepage
const { firstName, fullName, email, siteUrl } = personalInfo;
const currentEmployer = jobs.find((job) => job.current) || jobs[0];
const previousEmployers = jobs.filter(
  (job) => job.company != currentEmployer.company,
);

const ExternalLink = (props: IStyledLinkLinkProps) => {
  return (
    <StyledLink
      {...props}
      target="_blank"
      typographyProps={{
        variant: 'subtitle2',
      }}
    />
  );
};

const MoreLink = ({ to, label }: { to: string; label: string }) => {
  return (
    <>
      <StyledNavLink exact to={to} label="Learn More">
        <StyledButton variant="text">
          <StyledTypography
            fontVariant="titleLight"
            capitalized={true}
            bold={true}>
            {label}
          </StyledTypography>
        </StyledButton>
      </StyledNavLink>
    </>
  );
};

const InfoBar = () => {
  const resumePageRoute = useRouteMatch(`/resume`);
  const { scaleFactor } = useBreakpoints();
  const { md } = useBreakpoints();

  return (
    <>
      <Stack spacing={6 / scaleFactor}>
        <Stack spacing={3 / scaleFactor} alignItems={md ? 'center' : undefined}>
          <Avatar
            src={`${PUBLIC_URL}/images/me.jpg`}
            sx={{
              width: 160,
              height: 160,
            }}>
            N M
          </Avatar>
          <StyledTypography fontVariant="title" capitalized={true}>
            {fullName}
          </StyledTypography>
          <StyledLink
            to={`mailto:${email}`}
            bold={true}
            capitalized={true}
            label={`${email} ðŸ“§`}
            typographyProps={{
              variant: 'overline',
              color: 'text.disabled',
              letterSpacing: '0.15em',
            }}
          />
        </Stack>
        <Divider />
        <Stack spacing={3 / scaleFactor}>
          <StyledTypography
            variant="button"
            fontVariant="titleLight"
            bold={true}
            capitalized={true}>
            About
          </StyledTypography>
          <Typography variant="subtitle2" paragraph>
            HiðŸ‘‹, I&apos;m {firstName}. I like building things. I am a{' '}
            <ExternalLink
              to="https://svitvasad.ac.in/"
              label="SVIT, Vasad - GTU"
            />{' '}
            graduate, and currently working as a {currentEmployer.position} at{' '}
            <ExternalLink
              to={currentEmployer.link}
              label={currentEmployer.company}
            />
            . Before {currentEmployer.company} I was at{' '}
            {previousEmployers.map((job, index) => (
              <Fragment key={job.company}>
                <ExternalLink to={job.link} label={job.company} />{' '}
                {index < previousEmployers.length - 1 && 'and '}
              </Fragment>
            ))}
            .
          </Typography>
          <MoreLink
            to={resumePageRoute?.isExact ? '/about' : '/resume'}
            label={resumePageRoute?.isExact ? 'About me' : 'Learn More'}
          />
        </Stack>
        <Divider />
        <Stack direction="row">
          <ContactIcons />
        </Stack>
        <Typography
          display="inline"
          variant="overline"
          sx={{
            color: 'text.disabled',
          }}>
          &copy; {fullName}{' '}
          <StyledNavLink
            to="/"
            label={siteUrl}
            typographyProps={{
              variant: 'overline',
            }}
            disableActiveStyle={true}
          />
          .
        </Typography>
      </Stack>
    </>
  );
};

export default InfoBar;
