import {
  Box,
  CircularProgress,
  Divider,
  Paper,
  Skeleton,
  Stack,
  Theme,
} from '@material-ui/core';
import React, { ReactNode } from 'react';

import StyledTypography from '../components/StyledTypography';
import useBreakpoints from '../hooks/useBreakpoints';

interface IPageProps {
  header: ReactNode;
  subHeader?: ReactNode;
  children: ReactNode;
}

const PageContentLayout = ({ header, subHeader, children }: IPageProps) => {
  const { scaleFactor } = useBreakpoints();
  return (
    <>
      <Paper variant="outlined">
        <Stack
          sx={{ px: 6 / scaleFactor, py: 6 / scaleFactor }}
          spacing={2 / scaleFactor}>
          {typeof header === 'string' && (
            <StyledTypography
              fontVariant="title"
              aria-label="title"
              capitalized={true}
              hoverEffect={true}>
              {header}
            </StyledTypography>
          )}

          {typeof header !== 'string' && <>{header}</>}
          {subHeader && typeof subHeader === 'string' && (
            <StyledTypography
              fontVariant="subtitle"
              variant="overline"
              aria-label="subtitle"
              capitalized={true}>
              {subHeader}
            </StyledTypography>
          )}

          {subHeader && typeof subHeader !== 'string' && <>{subHeader}</>}
        </Stack>
        <Divider />
        <Box sx={{ p: 6 / scaleFactor }}>{children}</Box>
      </Paper>
    </>
  );
};

export default PageContentLayout;

const PageContentLayoutPlaceholder = () => {
  return (
    <>
      <PageContentLayout
        header={
          <>
            <Skeleton height={36} />
          </>
        }
        subHeader={
          <>
            <Skeleton height={30} />
          </>
        }>
        <Box
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          height="55vh">
          <CircularProgress
            sx={{
              color: (theme: Theme) => theme.palette.text.disabled,
            }}
          />
        </Box>
      </PageContentLayout>
    </>
  );
};

export { PageContentLayoutPlaceholder };
