import EmailRoundedIcon from '@material-ui/icons/EmailRounded';
import FacebookRoundedIcon from '@material-ui/icons/FacebookRounded';
import GitHubIcon from '@material-ui/icons/GitHub';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import React from 'react';
import TwitterIcon from '@material-ui/icons/Twitter';
import personalInfo from './personalInfo';

interface IContact {
  link: string;
  label: string;
  icon: React.ReactNode;
}

const contacts: IContact[] = [
  {
    link: personalInfo.linkedInUrl,
    label: 'LinkedIn',
    icon: <LinkedInIcon />,
  },
  {
    link: personalInfo.githubUrl,
    label: 'Github',
    icon: <GitHubIcon />,
  },
  {
    link: personalInfo.twitterUrl,
    label: 'Twitter',
    icon: <TwitterIcon />,
  },

  {
    link: personalInfo.instagramUrl,
    label: 'Instagram',
    icon: <InstagramIcon />,
  },
  {
    link: personalInfo.facebookUrl,
    label: 'Facebook',
    icon: <FacebookRoundedIcon />,
  },
  {
    link: `mailto:${personalInfo.email}`,
    label: 'Email',
    icon: <EmailRoundedIcon />,
  },
];

export default contacts;
